/* Spinner for when it's translating a page */

.TnITTtw-cta-button-layout {
  position: relative;
  display: inline;
}

.TnITTtw-spinner {
  display: none;
  text-align: center;
  position: absolute;
  top: 0;
  width: 100%;
  user-select: none;
  -webkit-user-select: none;
}

@media screen and (max-width: 800px) {
  .TnITTtw-spinner {
    top: 28px;
  }
}

.TnITTtw-spinner.in-text {
  display: block;
  background: transparent;
  position: initial;
}

.TnITTtw-spinner.left {
  text-align: left;
}

.TnITTtw-spinner > div {
  width: 12px;
  height: 12px;
  background-color: rgb(0, 71, 46);

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.TnITTtw-spinner.in-text > div {
  background-color: #000;
}

.TnITTtw-spinner .TnITTtw-bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.TnITTtw-spinner .TnITTtw-bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% { 
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}
